import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
import { DirectorProvider } from '../../providers/director/director';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { ProfileProvider } from '../../providers/profile/profile';
import { DirectionserviceProvider } from '../../providers/directionservice/directionservice';
import { RatePage } from '../rate/rate';
declare var google;

/**
 * Generated class for the RatingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ratings',
  templateUrl: 'ratings.html',
})
export class RatingsPage {
  items: any[];

  constructor(public dServ: DirectionserviceProvider, public ph: ProfileProvider, public director: DirectorProvider, public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.ph.getRatingProfile().child(this.director.restuarant_Detail.key).on('value', userProfileSnapshot => {
      this.items = [];
      userProfileSnapshot.forEach( snap => {
        
        this.items.push({
          key: snap.key,
          review: snap.val().review,
          name: snap.val().name,
          date: snap.val().date
        });

       
        console.log(this.items)
        return false
    
        
      
      });
    })
  }

  showRate(){
    this.navCtrl.push(RatePage);
  }

}



